<script setup>
import { ref, onMounted,watch } from 'vue'
import {useArticlesStore} from '@/store/Articles.js'
import { usefieldstore } from '@/store/field.js'
import { Dataformat,formatDate,getLastWeekDate,getLastMonthDate,getLastYearDate } from '../../utils/data'
import { onShow } from '@dcloudio/uni-app'
import Tabswitch from '@/components/Tabswitch/Tabswitch.vue'
import ArticleCard from '@/components/ArticleCard/ArticleCard.vue'
import ArticlePolicyVue from '../../components/ArticleCard/ArticlePolicy.vue'

const listarticles = useArticlesStore()
const field = usefieldstore()

const activeTab = ref('policy')

// ÊêúÁ¥¢Ê†è
const searchbar = ref("")

// ÂΩìÂâçÊâìÂºÄÁöÑ‰∏ãÊãâÊ°Ü
const currentDropdown = ref(null)

// ÊòØÂê¶Á≤æÈÄâ
const isselected=ref(0)

// ÂàùÂßãÂä†ËΩΩÁä∂ÊÄÅ
const initialLoading = ref(false)

// ‰∏ãÊãâÂà∑Êñ∞Áä∂ÊÄÅ
const refreshTriggered = ref(false)

// ÂàùÂßãÂÄºËÆæ‰∏∫"ÂÖ®ÈÉ®"
const selectedDomain = ref({ field_id: 0,field_code: "", field_name: 'ÂÖ®ÈÉ®' })
const selectedTime = ref('ÂèëÂ∏ÉÊó∂Èó¥')

// Êó∂Èó¥ÂàóË°®
const timeList = ['ÂÖ®ÈÉ®', 'ÊúÄËøë‰∏ÄÂë®', 'ÊúÄËøë‰∏ÄÊúà', 'ÊúÄËøë‰∏ÄÂπ¥']

// ÂÆö‰πâÂèÇÊï∞Êü•ËØ¢ÁöÑÁªìÊûÑ‰Ωì
const Params = { 
  field_type: "",
  page: 0,
  is_selection: 0,
  article_title: "",
  release_time:"",
  article_type:activeTab.value.toUpperCase()
};

// ÁõëËßÜÂô®ÔºåÁõëÊéßactiveTabÁöÑÂèòÂåñ
watch(activeTab, (newVal, oldVal) => {
  console.log('Tab ÂèòÂåñ:', oldVal, '=>', newVal)
  if (newVal === 'news') {
    listarticles.resetpage(1)
	resetFilters()
	Params.article_type="NEWS"
  } else if (newVal === 'policy') {
    listarticles.resetpage(1)
	resetFilters()
	Params.article_type="POLICY"
  }
})

// Ëé∑ÂèñÁ©∫Áä∂ÊÄÅÊèêÁ§∫ÊñáÊ°à
function getEmptyMessage() {
  if (searchbar.value) {
    return `Êú™ÊâæÂà∞‰∏é"${searchbar.value}"Áõ∏ÂÖ≥ÁöÑÂÜÖÂÆπÔºåËØïËØïÂÖ∂‰ªñÂÖ≥ÈîÆËØçÂêß`
  }
  if (selectedDomain.value.field_id !== 0 || selectedTime.value !== 'ÂÖ®ÈÉ®') {
    return 'ÂΩìÂâçÁ≠õÈÄâÊù°‰ª∂‰∏ãÊöÇÊó†ÂÜÖÂÆπÔºåËØïËØïË∞ÉÊï¥Á≠õÈÄâÊù°‰ª∂'
  }
  return 'ÊöÇÊó∂ËøòÊ≤°ÊúâÂÜÖÂÆπÔºåËØ∑Á®çÂêéÂÜçÊù•ÁúãÁúã'
}

// ÈáçÁΩÆÁ≠õÈÄâÊù°‰ª∂
function resetFilters() {
  searchbar.value = ""
  selectedDomain.value = { field_id: 0,field_code: "", field_name: 'ÂÖ®ÈÉ®' }
  selectedTime.value = 'ÂÖ®ÈÉ®'
  
  // ÈáçÁΩÆÂèÇÊï∞
  Params.article_title = ""
  Params.field_type = ""
  Params.release_time = ""
  Params.page = 1
  
  // ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
  loadData()
}

// ‰∏ãÊãâÂà∑Êñ∞
async function onRefresh() {
  refreshTriggered.value = true
  Params.page = 1
  
  try {
    await listarticles.getlistpolicy(Params)
  } catch (error) {
    console.error('Âà∑Êñ∞Â§±Ë¥•:', error)
  } finally {
    refreshTriggered.value = false
  }
}

// Áªü‰∏ÄÁöÑÊï∞ÊçÆÂä†ËΩΩÊñπÊ≥ï
async function loadData() {
  initialLoading.value = true
  try {
    await listarticles.getlistpolicy(Params)
  } catch (error) {
    console.error('Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•:', error)
    uni.showToast({
      title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
      icon: 'none'
    })
  } finally {
    initialLoading.value = false
  }
}

// ÊêúÁ¥¢
function search() {
	Params.article_title=searchbar.value
	Params.page=1
  loadData()
  console.log("ÊêúÁ¥¢ÂÖ≥ÈîÆËØç:", searchbar.value)
}

// ÂèñÊ∂àÊêúÁ¥¢
function cancel() {
  searchbar.value = ""
  Params.page=1
  Params.article_title=searchbar.value
  loadData()
}

// Âä†ËΩΩÊõ¥Â§ö
function loadMore() {
  if (listarticles.loading || !listarticles.hasMore) {
    return
  }
  
  Params.page = listarticles.page + 1
  listarticles.getarticlemore(Params)
  console.log("Âä†ËΩΩÊõ¥Â§öÔºåÂΩìÂâçÈ°µÁ†Å:", Params.page)
}

// ÂàáÊç¢‰∏ãÊãâÊ°Ü
function toggleDropdown(type) {
  currentDropdown.value = currentDropdown.value === type ? null : type
}

// ÈÄâÊã©Á≠õÈÄâÈ°π
function selectOption(type, value) {
  if (type === 'domain') {
    if (value === null) {
      selectedDomain.value = { field_id: 0,field_code: "", field_name: 'ÂÖ®ÈÉ®' }
	  Params.page=1
	  Params.field_type=selectedDomain.value.field_code
      loadData()
    } else {
		console.log("valueÂÄºÔºö",value)
      selectedDomain.value = value
	  Params.page=1
	  Params.field_type=selectedDomain.value.field_code
	  console.log("params:",Params)
      loadData()
    }
  }

  if (type === 'time') {
	  console.log(value)
    selectedTime.value = value
	if (value === 'ÊúÄËøë‰∏ÄÂë®') {
	      Params.release_time = getLastWeekDate();
	    } else if (value === 'ÊúÄËøë‰∏ÄÊúà') {
	      Params.release_time = getLastMonthDate();
	    } else if (value === 'ÊúÄËøë‰∏ÄÂπ¥') {
	      Params.release_time = getLastYearDate();
	    } else {
	      Params.release_time = '';
	    }
	
	    Params.page = 1;
	    loadData()
  }

  currentDropdown.value = null
}

// Â§ÑÁêÜÊîøÁ≠ñÁÇπÂáª
const handlePolicyClick = (policyItem) => {
  console.log('ÁÇπÂáª‰∫ÜÊîøÁ≠ñ:', policyItem)
  uni.navigateTo({
    url: `/pages/detail/articledetail?id=${policyItem.article_id}`
  })
}

// Â§ÑÁêÜÊñ∞ÈóªÁÇπÂáª
const handleNewsClick = (newsItem) => {
  console.log('ÁÇπÂáª‰∫ÜÊñ∞Èóª:', newsItem.article_id)
  uni.navigateTo({
    url: `/pages/detail/articledetail?id=${newsItem.article_id}`
  })
}

// Ë∑≥ËΩ¨ËØ¶ÊÉÖ
function OnClick(id) {
	console.log("ÊµãËØïÁöÑId:"+id)
  uni.navigateTo({
    url: `/pages/detail/articledetail?id=${id}`
  })
}

// Âú®È°µÈù¢ÊòæÁ§∫Êó∂Âà§Êñ≠Êù•Ê∫ê
onShow(() => {
  const source = uni.getStorageSync('tabSource') || 'tabbar'
  field.getfield()
  
  if (source === 'switchTab') {
    console.log('Êù•Ê∫êÔºöÈÄöËøá uni.switchTab() Ë∑≥ËΩ¨');
	Params.is_selection=1
	Params.page=1
	loadData()
	Params.article_type="NEWS"
	loadData()
	Params.article_type=activeTab.value.toUpperCase()
  } else {
    console.log('Êù•Ê∫êÔºöÁî®Êà∑ÁÇπÂáª tabBar ËøõÂÖ•');
	isselected.value=0
	Params.page=1
	loadData()
	Params.article_type="NEWS"
	loadData()
	Params.article_type=activeTab.value.toUpperCase()
  }

  // Ê∏ÖÈô§Ê†áËÆ∞ÔºåÈÅøÂÖçÂπ≤Êâ∞‰∏ã‰∏ÄÊ¨°Ë∑≥ËΩ¨
  uni.removeStorageSync('tabSource')
})

</script>

